.default {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: rgb(85, 82, 79);
    opacity: 0.5;
}

.dark {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: rgb(55, 31, 2);
    opacity: 0.6;
}

.root {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    background-image: url(/public/img/AsiaPacific.jpeg);
    background-size: 100% 100vw;
}

.content {
    display: flex;
    margin: 20px auto;
    max-width: 600px;
    width: 100%;
}

